body {
  font-family: Gilroy;
  color: $text-primary;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
* {
  padding: 0;
  margin: 0;
  list-style: none;
  text-decoration: none;
  font-style: normal;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
svg {
  max-width: 100px;
  max-height: 100px;
}
main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}
button,
a {
  cursor: pointer;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
