body {
  color: #3b3937;
  min-height: 100vh;
  flex-direction: column;
  font-family: Gilroy;
  display: flex;
}

* {
  margin: 0;
  padding: 0;
  font-style: normal;
  text-decoration: none;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

svg {
  max-width: 100px;
  max-height: 100px;
}

main {
  flex-direction: column;
  flex-grow: 1;
  display: flex;
}

button, a {
  cursor: pointer;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  min-width: 200px;
  margin: 0 auto;
  padding: 0 10px;
}

@media screen and (min-width: 480px) {
  .container {
    min-width: 430px;
    padding: 0 15px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    min-width: 718px;
  }
}

