  text-shadow: 1px 1px 1px rgba(0, 0, 0, .5);
  background-color: #a9845c;
  border: none;
  font-weight: 700;
  transition: background-color .2s ease-in-out;
}

.button:hover, .button:focus {
  background-color: #926341;
}

.header {
  background-color: #3b3937;
  padding: 20px 0;
}

.header__container {
